<template>
    <div>
        <input v-model="sousou" placeholder="查询歌手" @change="slgedan"  style="width: 60%;margin-top: 20px;margin-bottom: 10px;height: 20px">
        <div class="morengs">
            <el-button v-for="(ser,a) in songer" :key="a" @click="son(ser)">{{ser}}</el-button>
        </div>
        <ul class="gedan">
            <li v-for="song in ret" :key="song.id" class="gelan" @click="bofang(song)">
                    <img :src="song.img1v1Url" alt="" class="mimg">
                <span  class="mname">{{song.name}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "geshou",
        data(){
            return{
                sousou:"",
                ret:[],
                songer:["薛之谦","李荣浩","张信哲","岳云鹏","孙燕姿","王菲"]
            }
        },
        methods: {
            son(ser){
                this.$axios.get("https://api.imjad.cn/cloudmusic/", {
                    params: {
                        type: "search",
                        s: ser,
                        search_type: 100
                    }
                }).then(res => {
                        this.ret = res.data.result.artists
                })
            },
            slgedan() {
                this.$axios.get("https://api.imjad.cn/cloudmusic/", {
                    params: {
                        type: "search",
                        s: this.sousou,
                        search_type: 100
                    }
                }).then(res => {
                    // console.log(res)
                    if (!res.data.result) {
                        alert("搜索结果为空！")
                    } else {
                        this.ret = res.data.result.artists
                        // console.log(this.ret)
                    }
                }).catch(err => {
                    console.log(err)
                })
            },
            bofang(song){
                this.$axios.get("https://api.imjad.cn/cloudmusic/",{
                    params:{
                        type: "artist",
                        id: song.id,
                        search_type: 100
                    }
                }).then(res=>{
                    // console.log(res.data.artist)
                    this.$router.push({
                        name:"geshoulist",
                        params:{
                            songlist: res.data.hotSongs,
                            listall: res.data.artist
                        }
                    })
                })
            }
        }
    }
</script>

<style scoped>
    .morengs{
        margin: 20px;
        width: calc(100% - 40px);
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .el-button{
        margin-bottom: 20px;
    }
    .el-button+.el-button{
        margin-left: 0px;
    }


    .gedan{
        width: 100%;
        margin: 10px auto;
        padding: 0;
        list-style: none;
        background: white;
        overflow: hidden;
    }
    .gelan{
        width: 95%;
        height: 80px;
        float: left;
        line-height: 80px;
        text-align: left;
        padding: 10px;
        cursor: pointer;
    }
    .gelan:hover img{
        opacity: 0.7;
    }
    .mimg{
        width:80px;
        height: 80px;
        border-radius: 5px;
        float: left;
    }
    .mname{
        margin-left: 20px;
        display: block;
        float: left;
        height: 80px;
        line-height: 80px;
    }
</style>
